import React from 'react'
import styled from 'styled-components';
// import logo from './assets/The_MovieDB_icon.png';

const Styles = styled.div`

.footer {
    background-image: url('https://www.themoviedb.org/assets/2/v4/footers_v2/BreakingBad-0541701c71d1c77aae2d4487be97b9a879fc41e881911fd3d32558f1f5f75d44.jpg'), radial-gradient(at 30% top, rgba(7, 64, 52, 1) 0%, rgba(8, 28, 36, 1) 70%);
    height: 500px;
    // box-sizing: border-box;
    // padding: 0;
    // background-position: center top;
    background-size: cover;
    background-repeat: no-repeat;
    // display: flex;
    // align-items: center;
}

.footer, nav {
    // display: flex;
    // justify-content: space-between;
    margin: 0 auto;
    font-size: .9em;
}

.footer, nav, a {
    color: rgb(177, 175, 172);
    outline:none;
    &:hover {
        color: #efefef;
        text-decoration:none;
    }
}

@media screen and (max-width:768px) {
    .footer-bottom {
      display: none;
    }
  }
`

function Footer() {
    return (
        <Styles>
            <nav className="text-white footer d-flex">
                <div className="container align-items-center">
                    <div className="d-flex justify-content-center">
                        <img width={130} height={116} src="./images/tmdb_logo.png" alt="logo" />
                    </div>
                    <div className="container row justify-content-center text-center pt-5">
                        <div className="col-md-3 col-6">
                            <h4 className="text-uppercase">The Basics</h4>
                            <ul className="list-unstyled">
                                <li><a href="">About TMDb</a></li>
                                <li><a href="">Contact Us</a></li>
                                <li><a href="">Support Forums</a></li>
                                <li><a href="">API</a></li>
                                <li><a href="">Blog</a></li>
                            </ul>
                        </div>
                        <div className="col-md-3 col-6">
                            <h4 className="text-uppercase">Get Involved</h4>
                            <ul className="list-unstyled">
                                <li><a href="">Contribution Bible</a></li>
                                <li><a href="">3rd Party Applications</a></li>
                                <li><a href="">Add New Movie</a></li>
                                <li><a href="">Add New TV Show</a></li>
                            </ul>
                        </div>
                        <div className="col-md-3 col-6">
                            <h4 className="text-uppercase">Community</h4>
                            <ul className="list-unstyled">
                                <li><a href="">Guidelines</a></li>
                                <li><a href="">Leaderboard</a></li>
                                <li><a href="">Forums</a></li>
                                <li><a href="">Twitter</a></li>
                            </ul>
                        </div>
                        <div className="col-md-3 col-6">
                            <h4 className="text-uppercase">Legal</h4>
                            <ul className="list-unstyled">
                                <li><a href="">Terms of Use</a></li>
                                <li><a href="">Privacy Policy</a></li>
                            </ul>
                        </div>
                    </div>
                    <div className="footer-bottom pt-5">
                        <p className="text-center">
                            &copy;{new Date().getFullYear()} - All Rights Reserved!
                    </p>
                    </div>
                </div>
            </nav>
        </Styles>
    )
}

export default Footer;